import { HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponseBase } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { DA_SERVICE_TOKEN, ITokenService } from '@delon/auth';
import { _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';
import { NzNotificationService } from 'ng-zorro-antd/notification';
import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { catchError, filter, mergeMap, switchMap, take, tap } from 'rxjs/operators';

const CODEMESSAGE = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: '',
};

/**
 * HTTP `app.module.ts`
 */
@Injectable()
export class DefaultInterceptor implements HttpInterceptor {
  //  Token  Token  Token 
  private refreshTokenEnabled = true;
  private refreshToking = false;
  private refreshToken$: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  constructor(private injector: Injector) {}

  private get notification(): NzNotificationService {
    return this.injector.get(NzNotificationService);
  }

  private get tokenSrv(): ITokenService {
    return this.injector.get(DA_SERVICE_TOKEN);
  }

  private get http(): _HttpClient {
    return this.injector.get(_HttpClient);
  }

  private goTo(url: string) {
    setTimeout(() => this.injector.get(Router).navigateByUrl(url));
  }

  private checkStatus(ev: HttpResponseBase) {
    if ((ev.status >= 200 && ev.status < 300) || ev.status === 401) {
      return;
    }

    const errortext = CODEMESSAGE[ev.status] || ev.statusText;
    this.notification.error(` ${ev.status}: ${ev.url}`, errortext);
  }

  private tryRefreshToken(ev: HttpResponseBase, req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    // 1TokenToken
    if (!this.refreshTokenEnabled || [`/api/token/refresh`].some((url) => req.url.includes(url))) {
      this.toLogin();
      return throwError(ev);
    }
    // 2 `refreshToking`  `true`  Token 
    if (this.refreshToking) {
      return this.refreshToken$.pipe(
        filter((v) => !!v),
        take(1),
        switchMap(() => next.handle(this.reAttachToken(req))),
      );
    }
    // 3 Token
    this.refreshToking = true;
    this.refreshToken$.next(null);

    return this.refreshTokenRequest().pipe(
      switchMap((res) => {
        // 
        this.refreshToking = false;
        this.refreshToken$.next(res);
        //  token
        this.tokenSrv.set(res);
        // 
        return next.handle(this.reAttachToken(req));
      }),
      catchError((err) => {
        this.refreshToking = false;
        this.toLogin();
        return throwError(err);
      }),
    );
  }

  /**
   *  Token 
   */
  private refreshTokenRequest(): Observable<any> {
    const model = this.tokenSrv.get();
    return this.http.post(`/api/token/refresh`, null, null, { headers: { refresh_token: model.refresh_token || '' } });
  }

  /**
   *  Token 
   *
   * >  `@delon/auth`  Token
   */
  private reAttachToken(req: HttpRequest<any>): HttpRequest<any> {
    //  NG-ALAIN  `SimpleInterceptor`
    const token = this.tokenSrv.get().token;
    return req.clone({
      setHeaders: {
        Authorization: `Bearer ${token}`,
      },
    });
  }

  private toLogin(): void {
    this.notification.error(``, ``);
    this.goTo('/passport/login');
  }

  private handleData(ev: HttpResponseBase, req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    //  `throw`  `_HttpClient`  `end()` 
    if (ev.status > 0) {
      this.http.end();
    }
    this.checkStatus(ev);
    // 
    switch (ev.status) {
      case 200:
        // restful
        // 
        //  { status: 1, msg: '' }
        //  { status: 0, response: {  } }
        // 
        // if (ev instanceof HttpResponse) {
        //   const body = ev.body;
        //   if (body && body.status !== 0) {
        //     this.injector.get(NzMessageService).error(body.msg);
        //     //  Pipesubscribe 
        //     // this.http.get('/').subscribe() 
        //     return throwError({});
        //   } else {
        //     //  `body`  `response` 
        //     return of(new HttpResponse(Object.assign(ev, { body: body.response })));
        //     // 
        //     return of(ev);
        //   }
        // }
        break;
      case 401:
        return this.tryRefreshToken(ev, req, next);
      case 403:
      case 404:
      case 500:
        this.goTo(`/exception/${ev.status}`);
        break;
      default:
        if (ev instanceof HttpErrorResponse) {
          console.warn('CORS', ev);
        }
        break;
    }
    if (ev instanceof HttpErrorResponse) {
      return throwError(ev);
    } else {
      return of(ev);
    }
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    // 
    let url = req.url;
    if (!url.startsWith('https://') && !url.startsWith('http://')) {
      url = environment.SERVER_URL + url;
    }

    const newReq = req.clone({ url });
    return next.handle(newReq).pipe(
      mergeMap((ev) => {
        // 
        if (ev instanceof HttpResponseBase) {
          return this.handleData(ev, newReq, next);
        }
        // 
        return of(ev);
      }),
      catchError((err: HttpErrorResponse) => this.handleData(err, newReq, next)),
    );
  }
}
